import { animationFrameScheduler, interval, scheduled } from 'rxjs';
import { repeat, map, throttle, share, scan } from 'rxjs/operators';
export const animationEvent$ = scheduled([animationFrameScheduler.now()], animationFrameScheduler).pipe(repeat(), share());
export function getAnimationEvent$(frameRate = 16) {
    return animationEvent$.pipe(throttle(() => interval(frameRate)));
}
export function getFPS$() {
    return animationEvent$.pipe(map(() => {
        const now = animationFrameScheduler.now();
        return {
            lastTimestamp: now,
            frameDistance: 0
        };
    }), scan((acc, cur) => {
        const { lastTimestamp } = acc;
        return {
            lastTimestamp: cur.lastTimestamp,
            frameDistance: cur.lastTimestamp - lastTimestamp
        };
    }), map(({ frameDistance }) => Math.ceil(1000 / frameDistance)), throttle(() => interval(250)));
}
